.menu-active {
    overflow: hidden;
}
.menu-active .mobile-menu-overlay {
    opacity: 1;
    visibility: visible;
}
.menu-active .mobile-menu {
    transform: translateX(0);
}

.mobile-menu-overlay {
    z-index: 888;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    opacity: 0;
    visibility: hidden;
    transition: all 0.2s;
}

.mobile-menu {
    width: 300px;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    z-index: 999;
    transform: translateX(-100%);
    transition: transform 0.2s;
}
.mobile-menu .language-chooser {
    text-transform: uppercase;
}
.mobile-menu .language-chooser > li {
    display: inline-block;
    opacity: 0.8;
}
.mobile-menu .language-chooser > li.active {
    opacity: 1;
}
.mobile-menu .language-chooser > li:first-child:after {
    content: "|";
    display: inline-block;
    margin: 0 3px 0 5px;
    opacity: 0.6;
}
.mobile-menu__nav {
    background: #fff;
    position: relative;
    overflow: hidden;
    overflow-y: auto;
    height: 100%;
}
.mobile-menu__nav > li a {
    display: block;
    padding: 18px 20px;
    color: #212121;
}
